import Bus from '../eventbus'
import { upImg } from '../assets/js/upimg.js'
export default {
  name: 'upimg',
  props: ['upnum'],
  data () {
    return {
      picshowList: [],
      imgnum: 1,  // 
      maxnum: false,
      toastext: '',
      showToast: false,
      toastP: 'top'
    }
  },
  created () {
    // 
    this.imgnum = this.upnum;
    // console.log('this.upnum: ',this.upnum);
  },
  mounted () {
    var vm = this;
    console.log('upimg: ',upImg);
  },
  methods: {
    // toast 
    toastFn (toastP, text) {
      this.toastP = toastP
      this.showToast = true
      this.toastext = text
    },
    upfile (e) {
      let vm = this;
      let _this = e.currentTarget;
      console.log('this.upnum: ',vm.upnum);
      console.log('vm.picshowList.length: ',vm.picshowList.length);
      console.log('...');

      if(vm.picshowList.length+1 == vm.imgnum) {
        vm.maxnum = true;
      }
      if(vm.picshowList.length == vm.imgnum){
        vm.toastFn('top', `${vm.upnum}`);
        console.log(`${vm.upnum}`);
        
      }else{
        // loading 
        // vm.$vux.loading.show({
        //   text: '...'
        // })
      }
      // 
      upImg( _this, function(){
        // ()
        console.log('...');
        // vm.toastFn('top', `...`);
        vm.$vux.loading.show({
          text: '...'
        })

      }, function(res){
        setTimeout(() => {
          vm.$vux.loading.hide();
        },500)
        // console.log('res: ',res);
        // binsrc: 
        // src: 
        if(res.src){
          vm.picshowList.push({
            binsrc: res.binsrc,
            src: res.src,
            time: new Date().getTime()
          })
          vm.upbus();
        }
      })
      setTimeout(function() {
        vm.$vux.loading.hide()
      },500);

    },
    //  xx 
    delpic (img) {
      let vm = this;
      img = img.currentTarget;
      let time = img.getAttribute('time');
      let imgPar = img.parentNode;
      for(var i=0; i<vm.picshowList.length; i++){
        if(vm.picshowList[i].time == time){
          // console.log('vm.picshowList[i]: ',vm.picshowList[i]);
          // 
          vm.picshowList.splice(i, 1);
          vm.upbus();
        }
      }
      if(vm.picshowList.length < vm.upnum){
        vm.maxnum = false;
      }
      // console.log('vm.picshowList: ',vm.picshowList);
    },
    upbus () {
      Bus.$emit('upimg', this.picshowList);
    },
    beforeDestroy () {
      this.upbus();
    },
    destroyed () {
      // 
      Bus.$off('upimg', this.upbus);
    }
  }
}



